import {Component, OnDestroy, OnInit} from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {AuthService} from '../../../services/auth.service';
import {takeWhile} from 'rxjs/operators';

@Component({
  selector: 'app-verify-account',
  templateUrl: './verify-account.component.html',
  styleUrls: ['./verify-account.component.css']
})
export class VerifyAccountComponent implements OnInit, OnDestroy {
  alive = true;
  constructor(private activeRoute: ActivatedRoute, private authService: AuthService) { }
  error = true;
  message: string;
  ngOnInit() {
      this.activeRoute.queryParams.pipe(takeWhile(() => this.alive)).subscribe(queryParams => {
        // do something with the query params
        if (queryParams['code'] && queryParams['user_id']) {
          this.verifyEmail(queryParams['user_id'], queryParams['code']);
        }
      });
  }

  verifyEmail(id, code) {
    this.authService.verifyEmail(id, code).pipe(takeWhile(() => this.alive)).subscribe(response => {
      console.log(response);
      this.error = response.error;
      this.message = response.message;
    });
  }

  ngOnDestroy() {
    this.alive = false;
  }

}
