import { Directive, ElementRef, OnInit , Input } from '@angular/core';
import {AuthService} from '../../services/auth.service';

@Directive({
    selector: '[appUserPermission]'
})
export class UserPermissionDirective implements OnInit {
    @Input() permission: string; // Required permission passed in
    constructor(private el: ElementRef, private auth: AuthService) { }
    ngOnInit() {
        if (!this.auth.hasPermission(this.permission)) {
              this.el.nativeElement.style.display = 'none';
        }
    }
}
