import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {map} from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  ADMIN = 1;
  VENDOR = 2;
  CUSTOMER = 3;

  constructor(private http: HttpClient) { }

  login(formData: User) {
    return this.http.post<any>('/api/login/', formData).pipe(map(user => {
      // login successful if there's a jwt token in the response
      if (user && user.token) {
          // store user details and jwt token in local storage to keep user logged in between page refreshes
          this.setUser(user);
      }

      return user;
    }));
  }

  userSignUp(formData) {
    return this.http.post<any>('/api/user_sign_up/', formData);
  }

  logout() {
    // remove user from local storage to log user out
    localStorage.removeItem('user');
    return this.http.get<any>('/api/logout/');
  }

  isAdmin() {
    const user = this.getUser();
    return user.type === this.ADMIN;
  }

  isVendor() {
    const user = this.getUser();
    return user.type === this.VENDOR;
  }

  isCustomer() {
    const user = this.getUser();
    return user.type === this.CUSTOMER;
  }

  private setUser(user) {
    localStorage.setItem('user', btoa(JSON.stringify(user)));
  }

  getUser() {
    let user: User = new User();
    if (localStorage.getItem('user')) {
      user = JSON.parse(atob(localStorage.getItem('user')));
    }
    return user;
  }

  changeUserPassword(formData) {
    return this.http.post<any>('/api/change_user_password/', formData);
  }

  getOtp(mobile) {
    return this.http.get<any>('/api/get_otp/', {params: {mobile: mobile}});
  }

  verifyEmail(user_id: string, verification_code: string) {
    return this.http.get<any>(`/api/verify_email/${user_id}/`, {params: {code: verification_code}});
  }

  hasPermission(permission: string) {
    const user = this.getUser();
    const userType = {admin: this.ADMIN, customer: this.CUSTOMER, vendor: this.VENDOR};
    let _type;
    if (user) {
      if (user.type === this.ADMIN) {
        _type = this.ADMIN;
      } else if (user.type === this.VENDOR) {
        _type = this.VENDOR;
      } else {
        _type = this.CUSTOMER;
      }
    }
    let flag = false;
    if (_type === this.CUSTOMER) {
      flag = true;
    } else if (_type === userType.vendor && _type !== userType.admin) {
      flag = true;
    } else if (_type === userType.admin && (_type !== userType.vendor && _type !== userType.customer)) {
      flag = true;
    }
    return flag;
  }

}

class User {
  username: string;
  password: string;
  type?: number;
  token?: string;
}
