import {Injectable} from '@angular/core';
import {HttpHandler, HttpHeaders, HttpInterceptor, HttpRequest} from '@angular/common/http';
import {AuthService} from '../services/auth.service';
import {LoadingBarService} from '@ngx-loading-bar/core';
import {finalize} from 'rxjs/operators';


@Injectable()
export class AuthInterceptor implements HttpInterceptor {

  constructor(public auth: AuthService, private loadingBar: LoadingBarService) {}

  intercept(req: HttpRequest<any>, next: HttpHandler) {
    // add authorization header with jwt token if available
    const currentUser = this.auth.getUser();
    if (currentUser.token) {
      const headers = new HttpHeaders({'Content-Type':  'application/json', 'Authorization': `Token ${currentUser.token}`});
      req = req.clone({headers: headers});
    }
    this.loadingBar.start();
    return next.handle(req).pipe(finalize(() => this.loadingBar.stop()));
  }
}
