import {Component, DoCheck, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {FormBuilder, FormGroup} from '@angular/forms';

@Component({
  selector: 'app-time-picker',
  templateUrl: './time-picker.component.html',
  styleUrls: ['./time-picker.component.css']
})
export class TimePickerComponent implements OnInit, DoCheck {
  @Output() getValue: EventEmitter<string> = new EventEmitter<string>();
  @Input() setValue: string;
  _setValue = '12:00 AM';
  hours = '01';
  minutes = '00';
  meridian = 'AM';
  form: FormGroup;
  constructor(private fb: FormBuilder) {
    this.form = this.fb.group({
      hours: [this.hours],
      minutes: [this.minutes],
      meridian: [this.meridian]
    });
  }

  ngOnInit() {
    this.onTimeChange();
  }

  ngDoCheck() {
    if (this.setValue) {
      console.log(this.setValue);
      this._setValue = this.setValue;
      this.onTimeChange();
      this.setValue = '';
    }
  }

  onTimeChange() {
    this._setValue = this._setValue ? this._setValue : '12:00 AM';
    if (this._setValue.length === 8) {
      this.hours = this._setValue.split(':')[0];
      this.meridian = this._setValue.split(' ')[this._setValue.split(' ').length - 1];
      const min = this._setValue.split(':')[this._setValue.split(':').length - 1];
      this.minutes = min.split(' ')[0];
      this.form.patchValue({
        hours: [this.hours],
        minutes: [this.minutes],
        meridian: [this.meridian]
      });
    }
  }

  onChangeInput(event, t: 'HH' | 'MM') {
    let val;
    val = +event.target.value ? event.target.value : '00';
    if (val.length === 1) {
      val = '0' + val;
    }
    if (t === 'HH') {
      this.hours = val;
      this.form.patchValue({hours: this.hours});
    } else if (t === 'MM') {
      this.minutes = val;
      this.form.patchValue({minutes: this.minutes});
    }
    this.updateValue();
  }

  updateValue() {
    const v = this.form.getRawValue();
    const t = `${v.hours}:${v.minutes} ${v.meridian}`;
    this.getValue.emit(t);
  }

  changeTime(obj, action: 'UP' | 'DOWN', t: 'HH' | 'MM') {
    let num: any;
    if (action === 'UP') {
      num = +obj + 1;
    } else if (action === 'DOWN') {
      num = +obj - 1;
    }
    num = this.fillZero(num);
    if (t === 'HH') {
      if (+num > 12) {
        num = '00';
      }
      if (+num < 0) {
        num = '12';
      }
      this.form.patchValue({hours: num});
      this.hours = num;
    } else if (t === 'MM') {
      if (+num > 60) {
        num = '00';
        this.hours = this.fillZero(+this.hours + 1);
        this.form.patchValue({hours: this.hours});
      }
      if (+num < 0) {
        num = '60';
        this.hours = this.fillZero(+this.hours - 1);
        this.form.patchValue({hours: this.hours});
      }
      this.form.patchValue({minutes: num});
      this.minutes = num;
    }
    this.updateValue();
  }

  fillZero(value) {
    if (value.toString().length === 1) {
      value = '0' + value.toString();
    }
    return value.toString();
  }



}
