import {Component, OnDestroy, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {BsModalRef} from 'ngx-bootstrap';
import {AuthService} from '../../../services/auth.service';
import {AlertService} from '../alert/alert.service';
import {takeWhile, switchMap, map} from 'rxjs/operators';
import {interval} from 'rxjs';

@Component({
  selector: 'app-user-sign-up',
  templateUrl: './user-sign-up.component.html',
  styleUrls: ['./user-sign-up.component.css']
})
export class UserSignUpComponent implements OnInit, OnDestroy {
  commonError: string;
  mobile_form: FormGroup;
  otp_form: FormGroup;
  form: FormGroup;
  isAlive = true;
  errorMsg;
  otp = null;
  step: number;
  timer = {minutes: 2, seconds: 0};
  timer_text: string;
  timeInterval: any;
  constructor(
    public bsModalRef: BsModalRef,
    private fb: FormBuilder,
    private authService: AuthService
  ) {
    this.mobile_form = this.fb.group({
      mobile: ['', [
        Validators.required,
        Validators.minLength(10),
        Validators.maxLength(10),
        Validators.pattern('^[0-9]*$')
      ]]
    });
  }


  ngOnInit() {
    this.step = 1;
  }

  ngOnDestroy() {
    this.isAlive = false;
  }

  onMobileSubmit() {
    const formData = this.mobile_form.getRawValue();
    this.getOtp(formData.mobile);
  }

  editMobileNumber() {
    this.step = 1;
  }

  buildOtpForm() {
    this.otp_form = this.fb.group({
      otp: ['', [Validators.required, Validators.pattern('^[0-9]*$')]]
    });
    // emit value in sequence every 1 second
    const source = interval(1000);
    // output: 0,1,2,3,4,5....
    this.timeInterval = source.subscribe(val => this.countdown());
  }

  buildSignUpForm() {
    this.form = this.fb.group({
      mobile: ['', Validators.required],
      email: ['', [Validators.required, Validators.email]],
      name: ['', [Validators.required]],
      password: ['', [Validators.required, Validators.minLength(6), Validators.pattern('.*\\S.*[a-zA-z0-9_-]')]],
      confirm_password: ['', [Validators.required, Validators.minLength(6), Validators.pattern('.*\\S.*[a-zA-z0-9_-]')]],
    });
    this.form.patchValue({
      mobile: this.mobile_form.get('mobile').value
    });
  }

  onSubmit() {
    this.authService.userSignUp(this.form.getRawValue()).pipe(takeWhile(() => this.isAlive)).subscribe(response => {
      if (response.error) {
        this.commonError = response.message;
        console.log(response.message);
      } else {
        this.step = 4;
        this.commonError = null;
      }
    });
  }

  onOTPSubmit() {
    // if otp matched
    if (this.otp.toString() === this.otp_form.get('otp').value) {
      this.buildSignUpForm();
      // Go to the next step
      this.step = 3;
      this.errorMsg = null;
      this.timeInterval.unsubscribe();
    } else {
      this.errorMsg = 'Invalid OTP';
    }
  }

  errorMessage(form: FormGroup, control: string, errorName: string) {
    return form.get(control).hasError(errorName) && (form.get(control).dirty || form.get(control).touched);
  }

  getOtp(mobile) {
    this.authService.getOtp(mobile).pipe(takeWhile(() => this.isAlive)).subscribe(response => {
      if (response.error) {

      } else {
        this.otp = response.result;
        console.log(this.otp);
        // initialize otp form
        this.buildOtpForm();
        // Go to the next step
        this.step = 2;
      }
    });
  }

  resendOTP() {
    this.getOtp(this.mobile_form.get('mobile').value);
    this.timer = {minutes: 2, seconds: 0};
    // emit value in sequence every 1 second
    const source = interval(1000);
    // output: 0,1,2,3,4,5....
    const subscribe = source.subscribe(val => this.countdown());
  }

  countdown() {
    let minutes = this.timer.minutes;
    let seconds = this.timer.seconds;
    this.timer_text = minutes + ' : ' + seconds;
    seconds -= 1;
    if (minutes < 0) {
      return;
    } else if (seconds < 0 && minutes !== 0) {
      minutes -= 1;
      seconds = 59;
    } else if (seconds < 10 && seconds.toString().length !== 2) {
      this.timer_text = minutes + ' : ' + '0' + seconds;
    }

    if (minutes === 0 && seconds === 0) {
      this.timeInterval.unsubscribe();
      this.timer_text = '';
    }
    this.timer = {minutes: minutes, seconds: seconds};
  }

}
