import {Component, OnDestroy, OnInit} from '@angular/core';
import {BsModalRef} from 'ngx-bootstrap';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {AuthService} from '../../../services/auth.service';
import {takeWhile} from 'rxjs/operators';
import {AlertService} from '../alert/alert.service';

@Component({
  selector: 'app-change-user-password',
  templateUrl: './change-user-password.component.html',
  styleUrls: ['./change-user-password.component.css']
})
export class ChangeUserPasswordComponent implements OnInit, OnDestroy {
  form: FormGroup;
  isAlive = true;
  response_error: string;
  constructor(
    public bsModalRef: BsModalRef,
    private fb: FormBuilder,
    private authService: AuthService,
    private alertService: AlertService
  ) {
    this.form = this.fb.group({
      old_password: ['', [Validators.required, Validators.pattern('.*\\S.*[a-zA-z0-9_-]')]],
      new_password: ['', [Validators.required, Validators.minLength(6), Validators.pattern('.*\\S.*[a-zA-z0-9_-]')]],
      confirm_password: ['', [Validators.required, Validators.minLength(6), Validators.pattern('.*\\S.*[a-zA-z0-9_-]')]]
    });
  }

  ngOnInit() {
  }

  ngOnDestroy() {
    this.isAlive = false;
  }

  onSubmit() {
    this.authService.changeUserPassword(this.form.getRawValue()).pipe(takeWhile(() => this.isAlive)).subscribe(response => {
      if (response.error) {
        this.response_error = response.message;
        this.alertService.error(response.message);
      } else {
        this.bsModalRef.hide();
        this.alertService.success(response.message);
      }
    });
  }

  errorMessage(control: string, errorName: string) {
    return this.form.get(control).hasError(errorName) && (this.form.get(control).dirty || this.form.get(control).touched);
  }
}
