import {Component, OnDestroy, OnInit} from '@angular/core';
import {CategoryService} from '../../services/category.service';
import {map, takeWhile} from 'rxjs/operators';
import {Subject} from 'rxjs';
import {BsModalRef, BsModalService} from 'ngx-bootstrap';
import {SearchResultsComponent} from '../search-results/search-results.component';

@Component({
  selector: 'app-banner',
  templateUrl: './banner.component.html',
  styleUrls: ['./banner.component.css']
})
export class BannerComponent implements OnInit, OnDestroy {
  isAlive = true;
  bannerTitle: string;
  bannerTagLine: string;
  selectedLocation: any = {name: 'Pune', id: 1};
  isSearchOpen: boolean;
  bsModalRef: BsModalRef;
  locationList: Array<any> = [
    {name: 'Pune', id: 1},
    {name: 'Delhi', id: 2},
    {name: 'Nashik', id: 3},
    {name: 'Banglore', id: 4},
    {name: 'Nagpur', id: 5},
    {name: 'Aurangabad', id: 6},
  ];
  searchItemList: Array<any> = [
    {name: 'Pune', id: 1},
    {name: 'Delhi', id: 2},
    {name: 'Nashik', id: 3},
    {name: 'Banglore', id: 4},
    {name: 'Nagpur', id: 5},
    {name: 'Aurangabad', id: 6},
  ];
  searchTerm$ = new Subject<string>();
  constructor(private categoryService: CategoryService, private bsModalService: BsModalService) { }

  ngOnInit() {
    this.getCategoryList();
    this.searchTerm$.next();
    this.bannerTitle = 'SAMPLE TEXT FOR WELLNESS';
    this.bannerTagLine = 'Get instant access to reliable and affordable services';
    this.getCategoryList();
  }

  searchResult() {
    this.bsModalRef = this.bsModalService.show(SearchResultsComponent, {
      ignoreBackdropClick: true, class: 'modal-lg full-width-modal'});
  }

  getCategoryList() {
    this.categoryService.search(this.searchTerm$)
      .pipe(map(data => data.result), takeWhile(() => this.isAlive))
      .subscribe(data => {
        this.searchItemList = data;
      });
  }

  ngOnDestroy() {this.isAlive = false; }

}
